'use strict';


var modal = document.getElementById("qwM");
var modal2 = document.getElementById("qwMM");
var modal3 = document.getElementById("qwMMM");

// Get the button that opens the modal
var btn = document.getElementById("qw");
var btn2 = document.getElementById("qww");
var btn3 = document.getElementById("qwww");

// Get the <span> element that closes the modal
var cancel = document.getElementsByClassName("close")[0];

// When the user clicks on the button, open the modal 
btn.onclick = function() {
  modal.style.display = "flex";
}
btn2.onclick = function() {
  modal2.style.display = "flex";
}
btn3.onclick = function() {
  modal3.style.display = "flex";
}

// When the user clicks on <span> (x), close the modal
span.onclick = function() {
  modal.style.display = "none";
  modal2.style.display = "none";
  modal3.style.display = "none";
}

// When the user clicks anywhere outside of the modal, close it
window.onclick = function(event) {
  if (event.target == modal) {
    modal.style.display = "none";
    modal2.style.display = "none";
    modal3.style.display = "none";
  }
}

document.getElementsByClassName("burger_button")[0].addEventListener("click", function(){
  $(".header_burger_main").toggleClass("active");
  $(".burger_button").toggleClass("active");
});